body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightslategrey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main_box {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.publication1 {
  width: 500px;
  height: auto;
  margin: 50px;
  background-color: #14212b;
  color: white;
  border-radius: 10px;
}

.box_top {
  width: inherit;
  height: 50px;
  display: flex;
}

.user_photo {
  width: 40px;
  height: 40px;
  margin: 10px;
  border-radius: 50%;
}

.info {
  width: inherit;
  height: 45px;
  margin: 5px 0;
}

.user_info {
  width: max-content;
  height: 25px;
  display: flex;
}

.user_content {
  width: max-content;
  
}

.user_name{
  width: max-content;
  height: 25px;
  font-weight: bold;
}

.user_nickname{
  width: max-content;
  height: 25px;
  opacity: 50%;
  margin-left: 10px;
}

.date{
  width: max-content;
  height: 25px;
  opacity: 50%;
  margin-left: 10px;
}

.menu {
  width: 20px;
  height: 17px;
  margin-right: 15px;
  margin-top: 5px;
}

.menu:hover {
  border-radius: 50%;
  background-color: dimgrey;
  opacity: 70%;
}

.box_middle {
  width: 450px;
  height: max-content;
  margin-left: auto;
}

.img {
  width: 422px;
  height: auto;
  margin-top: 10px;
  border-radius: 20px;
  
}

.box_botton {
  width: 400px;
  height: 30px;
  margin: 10px 45px 10px auto;
  display: flex;
  justify-content: space-between;
}

.download {
  display: flex;
}
.download_icon {
  transform: rotate(90deg);
  margin-bottom: 10px;
}

.butt {
  cursor: pointer;
}